<template>
  <div class="playlists-container">
      <!-- 顶部介绍框同步 -->
      <div class="top-card">
        <div class="icon-wrap">
          <!-- 封面 -->
          <img :src="topList.coverImgUrl" alt="">
        </div>
        <div class="content-wrap">
          <div class="tag">精品歌单</div>
          <!-- 标题 -->
         <!--  <div class="title">{{topList.name}}</div> -->
          <!-- 介绍 -->
          <div class="info">
           {{topList.description}}
          </div>
        </div>
        <!-- 背景图 -->
        <img class="bg" :src="topList.coverImgUrl" alt="">
        <div class="bg-mask"></div>
      </div>
      <!-- 标签栏 -->
      <div class="tab-container">
        <!-- tab栏 顶部 -->
        <div class="tab-bar">
          <span class="item active">全部</span>
          <span class="item">欧美</span>
          <span class="item">华语</span>
          <span class="item">流行</span>
          <span class="item">说唱</span>
          <span class="item">摇滚</span>
          <span class="item">民谣</span>
          <span class="item">电子</span>
          <span class="item">轻音乐</span>
          <span class="item">影视原声</span>
          <span class="item">ACG</span>
          <span class="item">怀旧</span>
          <span class="item">治愈</span>
          <span class="item">旅行</span>
        </div>
         <!-- tab的内容区域 -->
         <div class="tab-content">
           <div class="items">
             <div class="item" @click="toList" v-for="(item,index) in list" :key="index">
               <div class="img-wrap">
                 <div class="num-wrap">
                   播放量:
                   <span class=" num">{{item.playCount}}</span>
                 </div>
                 <img :src=" item.coverImgUrl" alt="">
                 <span class="iconfont icon-play"></span>
               </div>
               <p class="name">{{item.name}}</p>
             </div>
  
           </div>
         </div>
      </div>
      <!-- 分页 -->
      <el-pagination
      style="margin-top: 50px;"
        background
        layout="prev, pager, next"
        :total="1000"
        size="10">
      </el-pagination>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      //总条数
      total: 0,
      //页码
      page: 1,
      //顶部的推荐歌单
      topList: {},
      //歌单列表
      list: [],
      //当前选择的数据
      tag: '全部'
    }
  },
  created () {
    this.topData()
    this.listDate()
  },
  methods: {
    // 顶部的数据
    topData () {
      this.FFaxios({
        url: 'https://autumnfish.cn/top/playlist/highquality',
        methods: 'get',
        params: {
          limit: 1,
          //分类数据
          cat: this.tag
        }
      }).then(res => {
        // console.log(res);
        this.topList = res.data.playlists[0]
      })
    },
    listDate () {
      this.FFaxios({
        url: 'https://autumnfish.cn/top/playlist/',
        method: "get",
        params: {
          limit: 10 //十条
        }
      }).then(res => {
        console.log(res);
        this.list = res.data.playlists
      })
    },
    //跳转到歌单列表详情页面
    toList () {
      this.$router.push({ path: '/playlist' })
    }
  }
}
</script>
<style lang='scss'>
.playlists-container {
  //  顶部介绍框同步
  .top-card {
    padding: 20px;
    height: 200px;
    display: flex;
    position: relative;
    overflow: hidden;
    border-radius: 5px;
    .icon-wrap {
      margin-right: 20px;
      z-index: 1;
      img {
        width: 160px;
        height: 160px;
      }
    }
    .content-wrap {
      z-index: 1;
      .tag {
        margin: 0;
        color: #dfac67;
        border: 1px solid #dfac67;
        width: 100px;
        height: 30px;
        border-radius: 5px;
        font-size: 16px;
        line-height: 30px;
        text-align: center;
        cursor: pointer;
      }
      .title {
        color: white;
        padding-top: 10px;
      }
      .info {
        color: #888482;
        font-size: 14px;
        padding-top: 5px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 5; //显示的行数
      }
    }
    // 背景图
    .bg {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 0;
      filter: blur(20px);
    }
    .bg-mask {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }
  .tab-container {
    padding-top: 30px;
    /* 导航栏 */
    .tab-bar {
      display: flex;
      justify-content: flex-end;
      .item {
        cursor: pointer;
        font-size: 15px;
        color: gray;
        margin-right: 20px;
        &.active {
          color: #dd6d60;
        }
      }
    }
    /* 内容 */
    .tab-content {
      margin-top: 20px;
      .items {
        display: flex;
        flex-wrap: wrap;
        .item {
          width: 200px;
          margin: 10px;
          position: relative;
          overflow: hidden;
          cursor: pointer;
          .img-wrap {
            position: relative;
            &:hover {
              .num-wrap {
                top: 0;
              }
              .iconfont {
                opacity: 1;
              }
            }
            .num-wrap {
              position: absolute;
              left: 0;
              font-size: 14px;
              color: white;
              line-height: 30px;
              background: rgba(0, 0, 0, 0.5);
              height: 30px;
              width: 100%;
              padding-left: 5px;
              top: -30px;
            }
            img {
              width: 200px;
              height: 200px;
              border-radius: 5px;
            }
            .iconfont {
              position: absolute;
              background: rgba(255, 255, 255, 0.8);
              bottom: 13px;
              right: 10px;
              font-size: 20px;
              color: #dd6d60;
              border-radius: 50%;
              background-color: #fff;
              width: 40px;
              height: 40px;
              display: flex;
              justify-content: center;
              align-items: center;
              opacity: 0;
              &::before {
                transform: translateX(3px);
              }
            }
          }
          .name {
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            font-size: 14px;
          }
        }
      }
    }
  }
  .el-pagination {
    text-align: center;
  }
}
</style>